import { ComponentProps } from "react"
import { styled } from "@rp/react/theme"
import { Box } from "@rp/react/web/mui/core"

type HeaderContainerProps = ComponentProps<typeof Box> & {
  top?: number
  height?: number
  backgroundColor?: string
}

const HeaderContainer = styled(Box, { name: "HeaderContainer" })<HeaderContainerProps>(
  ({ theme, top, height = 56, backgroundColor }) => ({
    borderBottomWidth: "1px",
    borderBottomColor: "#5E5E5E",
    borderBottomStyle: "solid",
    display: "flex",
    position: "sticky",
    top: `${top}px`,
    width: "100%",
    zIndex: 3,
    backgroundColor: backgroundColor || theme.palette.background.paper,
    maxWidth: "100vw",
    height: `${height}px`,
    [theme.breakpoints.down("md")]: {
      height: "84px",
    },
  }),
)

const HeaderContent = styled(Box, {
  name: "HeaderContent",
  shouldForwardProp: prop => prop !== "isMobile",
})<{ isMobile?: boolean }>(({ isMobile }) => {
  return {
    display: "flex",
    flexWrap: isMobile ? "wrap" : undefined,
    flexGrow: 1,
    minWidth: 0,
    gap: 1,
  }
})

const LeftContent = styled(Box, { name: "LeftContent" })(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  flexShrink: 1,
  minWidth: 0,
  flexBasis: "100%",
  marginTop: 1.5,
  [theme.breakpoints.down("md")]: {
    marginTop: 0,
  },
}))

const RightContent = styled(Box, { name: "RightContent" })(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  flexShrink: 0,
  flexGrow: 1,
  justifyContent: "flex-start",
  [theme.breakpoints.down("md")]: {
    justifyContent: "space-between",
  },
}))

export { HeaderContainer, HeaderContent, LeftContent, RightContent }
